<template>
  <section class="profile">
    <div class="container">
      <h3>
        Account Info
      </h3>
      <div class="row">
        <div class="col-6">
          <div class="profile-avatar">
            <div class="profile-avatar-img-container">
              <img
                src="@/assets/images/face.jpg"
                alt=""
                class="profile-avatar-img"
              />
            </div>

            <button type="submit" class="change-avatar-btn">
              Change Avatar
            </button>
          </div>
          <div class="profile-form">
            <FormulateForm
              @submit="handleChangeProfile"
              name="registerForm"
              v-model="profileValues"
            >
              <FormulateInput
                type="email"
                label="Email"
                name="email"
                validation="required"
              />

              <FormulateInput
                type="text"
                label="Name"
                name="name"
                validation="required"
              />

              <FormulateInput
                type="tel"
                label="Phone"
                name="phone"
                validation="required"
              />

              <FormulateInput
                type="password"
                label="Password"
                name="password"
                validation="required"
              />

              <button type="submit" class="sbmt-btn" :disabled="btnDisabled">
                <span class="register-btn-text" v-if="!btnDisabled">
                  Save Changes
                </span>
                <span class="register-btn-loading" v-if="btnDisabled">
                  <circle7></circle7>
                </span>
              </button>
            </FormulateForm>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import { Circle7 } from "vue-loading-spinner";
export default {
  components: {
    Circle7,
  },
  data() {
    return {
      profileValues: {},
      btnDisabled: false,
    };
  },
  methods: {
    handleChangeProfile() {
      this.btnDisabled = true;
    },
  },
};
</script>

<style scoped lang="scss">
.profile-avatar {
  margin-bottom: 20px;
  display: flex;
}
.profile-avatar-img {
  border-radius: 50%;
}
.change-avatar-btn {
  background-color: transparent;
  border: none;
  font-weight: bold;
  color: $textDark;
}
</style>
